.heading {
  display: grid;
  grid-template: auto / 1fr 1fr;
  font-family: 'helvetica';
  grid-column: 1/3;
  grid-row: 1/2; }
  .heading .heroImage {
    width: 50%;
    height: 620px;
    background: url("/static/laughing-women.jpg");
    background-repeat: no-repeat;
    background-position: left top;
    background-size: cover;
    position: absolute;
    z-index: -1; }
  .heading .heroTitle {
    grid-column: 1/3;
    grid-row: 2/3;
    justify-self: center;
    font-family: 'helvetica-bold';
    font-size: 95px;
    margin-left: 12px;
    margin-top: 160px;
    z-index: 10; }
    .heading .heroTitle .culture {
      color: white; }
  .heading .subTitle {
    font-size: 25px;
    font-family: 'helvetica';
    grid-row: 3/4;
    grid-column: 2/3;
    padding-left: 16px; }

.body {
  padding: 210px;
  background-color: #111111;
  margin-top: 300px;
  display: flex;
  justify-content: center; }
  .body .innerBody {
    max-width: 1100px; }
  .body .companyCards {
    margin-top: 60px;
    display: grid;
    justify-items: center;
    grid-template-rows: auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 16px; }
  .body .card {
    width: 100%; }

@media screen and (max-width: 720px) {
  .heading {
    display: grid;
    grid-template: auto auto 350px 100% / 1fr; }
    .heading .heroImage {
      width: 100%;
      height: 100%; }
    .heading .heroTitle {
      grid-column: 1/2;
      text-align: center;
      color: white;
      font-size: 75px; }
    .heading .subTitle {
      grid-column: 1/2;
      padding-left: 16px;
      color: white;
      text-align: center; }
    .heading .valuePickerCard {
      display: none; } }
