.button-badge {
  margin: 6px 0;
  --mdc-theme-primary: #6250ff;
  --text-primary-on-light: black;
  --text-primary-on-dark: white;
  height: 26px;
  width: 250px; }

.mdc-button__label {
  display: flex;
  align-items: center; }

.mdc-button__text {
  margin-left: 16px;
  display: block;
  transition: 300ms;
  font-size: 11px;
  font-weight: 600;
  text-align: start; }

div.toggled .mdc-button__text {
  margin-left: 0px; }

div.toggled .mdc-button__circle {
  transform: scale(100); }

div.toggled .mdc-button__text {
  z-index: 2; }

.mdc-button__circle {
  border-radius: 50%;
  width: 5px;
  height: 5px;
  transition: 400ms;
  position: absolute; }
