.card {
  cursor: pointer;
  width: 100%;
  height: 304px;
  contain: content;
  border-radius: 16px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: white;
  animation: shimmy 3s infinite; }
  .card .cardBody {
    height: 180px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; }
    .card .cardBody img {
      width: 80px;
      height: 80px;
      border-radius: 16px; }
    .card .cardBody h2 {
      margin: 8px; }
    .card .cardBody p {
      margin: 10px;
      font-size: 14px; }
  .card .cardFooter {
    background-color: #6250ff;
    color: white;
    width: 100%;
    height: 100%;
    padding-top: 16px;
    font-size: 14px;
    text-align: center; }
    .card .cardFooter p {
      margin: 6px;
      font-weight: 300; }

.card:hover {
  animation: hoverAnimation ease-in-out 0.8s both; }

@keyframes hoverAnimation {
  0% {
    transform: translate(0px, 0px) perspective(250px); }
  100% {
    transform: translate(5px, 5px) perspective(500px) rotateY(-15deg);
    box-shadow: 0 3px 6px rgba(82, 82, 82, 0.637), 0 3px 6px rgba(63, 63, 63, 0.514); } }
